    height: 7vh;
}

.image-icon {
    padding: 0.5vh 0.25vh 0.5vh 0.25vh;
    max-width: 6vw;
    max-height: 6vh;    
}

.image-icon:hover {
    cursor: pointer;
}

/* Next & previous buttons */
.image-icon-prev,
.image-icon-next {
    position: absolute;
    cursor: pointer;
    font-weight: bold;    
    font-size: 5.25vh;    
}

.image-icon-prev {
    left: 0;
}

.image-icon-next {
    right: 0;
}

/* On hover, add a black background color with a little bit see-through */
.image-icon-prev:hover,
.image-icon-next:hover {
    background-color: rgba(0, 0, 0, 0.8);
}
/* The Modal (background) */
.modalhide {
    display: none; /* Hidden by default */
  }
  
.modalshow {
  display: flex; /* Hidden by default */
  position: absolute; /* Stay in place */
  z-index: 1; /* Sit on top */
  justify-content: center;
  align-items: center;  
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content Box */
.modal-content {
  display: inline-block;
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  margin-left: -2vw;
  padding-right: 2vw;
  font-size: 2vw;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
